.pagination {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    width: 100%;
    background-color: #549D99;
    padding: 5% 0;
    border-top: 2px black solid;
    border-bottom: 2px black solid;
}

.pagination input {
    padding: 2%;
    font-size: 1.2rem;
    margin: 1%;
    background-color: #612F2E;
    color: white;
}

.pagination input:disabled {
    background-color: #361a19;
}

.pagination input:hover {
    background-color: #904241;
}

.pagination input:disabled:hover {
    background-color: #361a19;
}

@media screen and (min-width:1024px) {
    .pagination {
        padding: 2% 0;
    }

    .pagination input {
        padding: 1%;
    }
}