.categories {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    background-color: #549D99;
    width: 100%;
    border-top: 2px black solid;
}

.categories input {
    margin: 3% 2%;
    padding: 3% 0;
    width: 46%;
    background-color: #612F2E;
    color: white;
    border-radius: 5px;
    font-size: 1.2rem;
}

.categories input:hover {
    background-color: #904241;
}

@media screen and (min-width:1024px) {
    .categories input {
        width: 20%;
        margin: 1%;
        padding: 1% 0;
    }
}