section {
    margin: 0 5%;
    width: 80%;
}

article {
    margin-bottom: 15%;
    border: 2px black solid;
    border-radius: 5px;
    margin-bottom: 5%;
    padding: 3%;
}



@media screen and (min-width:768px) {
    section {
        width: 90%;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
    }
}