.wrapper {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

main {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
}

img {
    max-width: 100%;
}

* {
    font-family: 'Grenze', serif;
}

h1,h2,h3,h4,h5,h6 {
    font-family: 'Grenze Gotisch', cursive;
    text-align: center;
}

h2 {
    font-size: 1.4rem;
}