article {
    background-color: #549D99;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}

article img {
    border-radius: 5px;
    border: 2px black solid;
}

article a {
    text-decoration: underline;
    color: black;
}

article a:hover {
    color: white;
}

article p {
    font-size: 1.1rem;
}



@media screen and (min-width:768px) {
    article {
        width: 40%;
    }
}